body {
    background: black;
    font-family:'IBM Plex Mono', monospace;
}

.beep-col {
    flex: auto;
    width: 100%;
    flex-direction: column;
    display: flex;
}

div.beep {
    padding: 2px;
    flex: auto;
    height: 100%;


}

div.beep-inner {
    height: 100%;
    width: 100%;
    /* background: rgb(41, 41, 41); */
    border: #73541a 1px solid;
    border-radius: 2px
}
.beep-col.odd div.beep-inner{
    /* background: rgb(61, 61, 61); */
    border: #463d2d 1px solid;
}

.beep-col.odd div.beep-inner:hover, .beep-col div.beep-inner:hover{
    border-color: orange !important;
}

.beep-col div.beep-inner.active {
    /* background: rgba(255, 177, 118, 0.24); */
    border-color: orange !important;

}

.beep-col div.beep-inner.on {
    background: orange;
    background: url('beep.png');
    background-size: 66px;
    box-shadow: orange 0px 0px 11px 1px;
}

.beep-col div.beep-inner.on.active {
    background: rgb(255, 231, 186);
}

.beep.active,
body .beep.active p {
    color: chartreuse;
}

.clearboth {
    clear: both;
    height: 1px;
}

.pull-right{float:right;}



