body .appbar {
    background: #0c0c0ccc;
    color: orange;
    border-bottom: 1px solid #1f1f1f;
    display:block;
    
}

body .toolbar {
    min-height: 52px;
    display: block;
    position: relative;
}

body .toolbar .playstop {
    width: 118px;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
}

body .toolbar .tempo-slider{
    width: 342px; position: absolute; top: 0; right: 24px;

}
body .toolbar .tempo-slider p{
    position: absolute;
    color: #606265;
    left: 0;
    top: 18px;
}
body .toolbar .tempo-slider .slider{
    position: absolute;
    right: 0;
    top: 16px;
}



body .playstop svg {
    font-size: 32px;
    padding: 9px;
}
body .playstop span{
    padding: 4px;
    /* display: inline-block; */
}


.main {

    margin: 59px auto 0 auto;
    width: inherit;
    position: fixed;
    top: 0;
    bottom: 25%;
    left: 7px;
    right: 7px;
    display: flex;
}

.ptest{clear:both; color: orangered; display: none;}

.orange-text{color: orange;}



