Y8b  d8 `8b  d8' 88  V888    88    88 `88. `8b  d8' 88booo. db   8D 
 `Y88P'  `Y88P'  VP   V8P    YP    88   YD  `Y88P'  Y88888P `8888Y' 
*/

body .slider {
    clear: both;
    padding: 12px 0;
    width: 250px;
    touch-action: none;
}

.controls {
    width: 294px;
    margin: auto;  
    position: fixed;
    top: 53px;
    left: 0;
    bottom: 0;
    padding: 0 ;
    background: #090909;
    transition: margin-left 200ms ease-in-out;
    z-index: 1000;
    border-right: 1px solid #231e16;
}

.icon {
    fill: orange;
}

body .slider p {
    color: #737373;
}


.controls.open{
    display:block;
    margin-left: 0;
    overflow-y: auto;
    scrollbar-width:none;
    -ms-overflow-style: none;  /* IE and Edge */
}
.controls.open::-webkit-scrollbar{
    display: none;
}
.controls.closed{
    display:block;
    margin-left: -330px;
}
.main.show-controls{
    padding-left: 296px;
    transition: padding-left 200ms ease-in-out;
}

.controls .toolbar{
    color:orange;
    padding: 5px 12px 8px 0;
    border-bottom: 1px solid #252525;
    overflow: hidden;
    margin: 0;
    min-height: 24px;
}

/*
KEYS
*/
#keyboard-wrapper{
    position: fixed;
    top: 75%;
    left: 7px;
    right: 7px;
    bottom: 7px;
}
#keyboard{clear: both; width: 100%; height: 100%; display:flex;}
#keyboard .key{width: 100%; height: 100%;  display: inline-block;  }
#keyboard .key .beep-inner{width: 100%; height: 100%;  background-image: url("images/5-reg.png"); position:relative;  }
#keyboard .key .beep-inner:hover{background-image: url("images/5-active.png");  }
#keyboard .key .beep-inner.active{background-image: url("images/bg-press2.png");  }

#keyboard-wrapper.show-controls{
    margin-left: 296px;
    transition: padding-left 200ms ease-in-out;
}

.icon:hover{ opacity: .8; cursor: pointer;}
.settings-icon svg{transition: transform .5s;}
.settings-icon:hover svg{transform: rotate(180deg);}

.key-label {
    background: #00000082;
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: 33px;
    color: orange;
    user-select:none;
    text-transform: uppercase;
}
#keyboard .key .beep-inner.active .key-label{opacity: 0.1;}

ul.wave-buttons, ul.wave-buttons li{padding: 0; margin: 0; list-style: none;}
ul.wave-buttons{display: flex;}
ul.wave-buttons li{padding: 8px;}
ul.wave-buttons li.active img, ul.wave-buttons li:hover img{opacity: 1;}
ul.wave-buttons li img{width: 33px; height: auto; opacity: 0.3;}

p.slider-label{margin-bottom: 0}

body .controls-wrapper{margin: 5px;
    border: 1px solid #342d21;
    width: 259px;
    padding: 12px;
    background: #090909;}


.controls-title{color:orange; letter-spacing: 6px;}